export default {
  bind(el, binding, vnode, oldVnode) {
    if (!binding.value) {
      return;
    }
    const headerEl = el.querySelector('.el-dialog-x__header');
    const dragEl = el.querySelector('.el-dialog-x');
    headerEl.style.cursor = 'move';
    vnode.context.initDragLeft = dragEl.style.left + '';
    vnode.context.initDragTop = dragEl.style.top + '';
    //  ie dom.currentStyle  window.getComputedStyle(dom, null);
    const currStyle = dragEl.currentStyle || window.getComputedStyle(dragEl, null);
    headerEl.onmousedown = (e) => {
      // 
      dragEl.className += ' el-dialog-x--move';
      const disX = e.clientX - headerEl.offsetLeft;
      const disY = e.clientY - headerEl.offsetTop;

      // px 
      let styL, styT, minL, maxL, minT, maxT;

      // ie 50% px
      if (currStyle.left.indexOf('%') > -1) {
        styL = +document.body.clientWidth * (+currStyle.left.replace(/\%/g, '') / 100);
        styT = +document.body.clientHeight * (+currStyle.top.replace(/\%/g, '') / 100);
      } else {
        styL = +currStyle.left.replace(/\px/g, '');
        styT = +currStyle.top.replace(/\px/g, '');
      };
      // 10px
      var mg = 10;
      minL = dragEl.clientWidth / 2 + mg;
      minT = mg;
      maxL = document.body.clientWidth - dragEl.clientWidth / 2 - mg;
      maxT = document.body.clientHeight - dragEl.clientHeight - mg;
      document.onmousemove = function(e) {
        // 
        window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
        // 
        const l = e.clientX - disX + styL;
        const t = e.clientY - disY + styT;
        // window.console.log('3:' + l + ':' + t);
        // window.console.log('4:' + disX + ':' + disY);
        // 
        if (l >= minL && l <= maxL) {
          dragEl.style.left = `${l}px`;
        }
        if (t >= minT && t <= maxT) {
          dragEl.style.top = `${t}px`;
        }
        // 
        // binding.value({x:e.pageX,y:e.pageY})
      };

      document.onmouseup = function(e) {
        dragEl.className = dragEl.className.replace(' el-dialog-x--move', '');
        document.onmousemove = null;
        document.onmouseup = null;
      };
    };
  }
};



