<template>
  <div class="el-lazy">
    <div v-if="isInit" class="el-lazy__comp">
      <slot :loading="compLoading"></slot>
    </div>
    <div v-else-if="$slots.skeleton" class="el-lazy__skeleton">
      <slot name="skeleton"></slot>
    </div>
    <div v-else class="el-lazy__wait" :style="waitStyle" v-loading="waitLoading">
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ElLazy',
    props: {
      timeout: {
        type: Number,
        default: 0
      },
      viewport: {
        type: typeof window !== 'undefined' ? window.HTMLElement : Object,
        default: () => null
      },
      threshold: {
        type: String,
        default: '0px'
      },
      direction: {
        type: String,
        default: 'vertical'
      },
      maxWaitingTime: {
        type: Number,
        default: 50
      },
      waitAreaHeight: {
        type: String,
        default: '0'
      }
    },
    data() {
      return {
        isInit: false,
        timer: null,
        io: null,
        compLoading: false
      };
    },
    created() {
      // timeouttimeout
      if (this.timeout > 0) {
        this.timer = setTimeout(() => {
          this.init();
        }, this.timeout);
      }
    },
    mounted() {
      if (this.timeout <= 0) {
        // 
        let rootMargin;
        switch (this.direction) {
          case 'vertical':
            rootMargin = `${this.threshold} 0px`;
            break;
          case 'horizontal':
            rootMargin = `0px ${this.threshold}`;
            break;
        }
        // 
        this.io = new window.IntersectionObserver(this.intersectionHandler, {
          rootMargin,
          root: this.viewport,
          threshold: [ 0, Number.MIN_VALUE, 0.01]
        });
        this.io.observe(this.$el);
      }
    },
    beforeDestroy() {
      // 
      if (this.io) {
        this.io.unobserve(this.$el);
      }
    },
    methods: {
      // 
      intersectionHandler(entries) {
        //   0
        if (entries[0].isIntersecting || entries[0].intersectionRatio) {
          this.init();
          this.io.unobserve(this.$el);
        }
      },
      // 
      init() {
        // 
        this.$emit('before-init');
        // 
        this.compLoading = true;
        // 
        // requestAnimationFrame
        this.requestAnimationFrame(() => {
          this.isInit = true;
          this.$emit('init');
        });
      },
      requestAnimationFrame(callback) {
        // 
        // 
        setTimeout(() => {
          if (this.isInit) return;
          callback();
        }, this.maxWaitingTime);
        // requestAnimationFrame 
        return (window.requestAnimationFrame || ((callback) => setTimeout(callback, 1000 / 60)))(callback);
      }
    },
    computed: {
      waitStyle: function() {
        return {
          height: this.waitAreaHeight
        };
      },
      waitLoading: function() {
        return this.waitAreaHeight !== '0' && !this.compLoading;
      }
    }
  };
</script>


